.navbar {
    background: rgb(145, 29, 29);
    height: 5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: sticky;
    top: 0;
    z-index: 999;
  }
  
  
  .navbar-logo {
    color: #fff;
    justify-self: start;
    margin-left: 20px;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-items: center;
  }
  
  .fa-laptop-code {
    display: flex;
    margin-left: 0.5rem;
    font-size: 1.8rem;
  }
  .fa-hat-wizard {
    display: flex;
    margin-left: 0.5rem;
    font-size: 1.8rem;
  }
  
  .nav-menu {
    display: flex;
    list-style: none;
    text-align: center;

    width: 80vw;
    justify-content: end;
    margin-right: 2rem;
  }
  
  .nav-item {
    height: 70px;
    ;
  }
  
  .nav-links {
    color: #fff;
    display: flex;
    align-items: center;
    
    text-decoration: none;
    padding: 1rem 6rem;
    /* height: 100%; */
  }
  
  .nav-links:hover {
    border-bottom: 4px solid #fff;
    transition: all 0.2s ease-out;
  }
  
  .fa-bars {
    color: #fff;
  }
  
  .nav-links-mobile {
    display: none;
  }
  
  .menu-icon {
    display: none;
  }
  .btn-mobile{
      display: none;
  }
  
  @media screen and (max-width: 960px) {
    .NavbarItems {
      position: relative;
    }
  
    .nav-menu {
      display: flex;
      flex-direction: column;
      align-items: start;
      width: 35%;
      position: absolute;
      top: 3.5rem;
      left: -100%;
      opacity: 1;
      transition: all 0.5s ease;
    }
  
    .nav-menu.active {
      background: #242222;
      left: 0;
      opacity: 1;
      transition: all 0.5s ease;
      z-index: 1;
    }
  
    .nav-links {
      text-align: center;
      /* padding: 2rem; */
      width: 100%;
      /* display: table; */
    }
  
    .nav-links:hover {
      background-color: #fff;
      color: #242424;
      border-radius: 0;
    }
  
    .navbar-logo {
      position: absolute;
      top: 0;
      left: 0;
      transform: translate(25%, 50%);
    }
  
    .menu-icon {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      transform: translate(-100%, 60%);
      font-size: 1.8rem;
      cursor: pointer;
    }
  
    .fa-angle-double-down {
      color: #fff;
      font-size: 2rem;
    }
    .fa-laptop-code {
        margin-left: 0.1rem;
        font-size: 1.3rem;
      }
      .fa-hat-wizard {
        margin-left: 0.2rem;
        font-size: 1.3rem;
      }
